<template>
  <div class="home">
    <v-app id="inspire">
    <v-main>
      <v-container class="fill-height" fluid>
        <v-row align="center" justify="center">
          <v-col cols="12" sm="8" md="4">
            <v-card class="mx-auto" max-width="400" >
              <v-list-item two-line>
                <v-list-item-content >
                  <v-list-item-title  class="headline" >{{ city }}</v-list-item-title>
                  <v-list-item-subtitle >{{condition.main}} </v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>

              <v-card-text>
                <v-row align="center">
                  <v-col class="display-3" cols="6">{{ temp | roundUp }}&deg;C</v-col>
                  <v-col cols="6" v-if="condition.main == 'Rain'" >
                    <v-img
                    
                      src="../assets/rain.png"
                      alt="Sunny image"
                      width="92"
                    ></v-img>
                  </v-col>
                  <v-col cols="6" v-if="condition.main == 'Clouds'" >
                    <v-img
                    
                      src="../assets/clouds.png"
                      alt="Sunny image"
                      width="92"
                    ></v-img>
                  </v-col>
                  <v-col cols="6" v-if="condition.main == 'Clear'" >
                    <v-img
                    
                      src="../assets/sun.png"
                      alt="Sunny image"
                      width="92"
                    ></v-img>
                  </v-col>
                   
                  
                </v-row>
              </v-card-text>

              
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: "Home",
  props: {
    source: String,
  },
  data () {
    return {
    cityname:'',
    countrycode:'',
    ip: '',
    city: null,
    temp: null,
    condition: {
      main: null,
      description: null
    }
      
    }
  },
  mounted(){
    axios.get('https://api.ipify.org')
    .then(function(response){
      this.ip = response.data
      console.log(response.data)
    }.bind(this))
    .catch(function(error){
      console.log(error)
    })
    axios.get(`http://ip-api.com/json/${this.ip}`)
    .then(function(response){
      this.cityname = response.data.city
      this.countrycode = response.data.countryCode
      console.log(response.data)
    }.bind(this))
    .catch(function(error){
      console.log(error)
    })
    axios.get(`https://api.openweathermap.org/data/2.5/weather?q=${this.cityname},${this.countryCode},jp&units=metric&appid=b48b08c4ed296d3355571a6fb1695a10`)
    .then(function(response){
      this.city = response.data.name
      this.temp = response.data.main.temp
      this.condition = response.data.weather[0]
      this.description = response.data.weather[0]
      console.log(response.data)
    }.bind(this))
    .catch(function(error){
      console.log(error)
    })
    
    
  },
  
  filters: {
    roundUp(value){
      return Math.ceil(value)
    }
  },
  created() {
    this.$vuetify.theme.dark = true;
  }
};

</script>
<style>
.v-card{
  background-image: url(https://wallpaperplay.com/walls/full/9/b/d/121428.jpg);
  background-size: cover;
  background-position: Top;
}

</style>